/****** Style Star Rating Widget *****/

.rating {
  border: none;
  float: left;
}

.rating > input { display: none; }
.rating > label:before {
  margin: 5px;
  font-size: 1.25em;
  font-family: FontAwesome;
  display: inline-block;
  content: "\f005";
}

.sc_head{
  visibility: inherit;
  background-color:rgba(255, 0, 0, 0.0);
  padding-bottom: -100px;
  text-decoration: none;

  position: fixed;
  width: 100%;


